//後台Navbar
<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container-fluid">
      <a class="navbar-brand" href="#">後台系統</a>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarText"
        aria-controls="navbarText"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarText">
        <ul class="navbar-nav mb-2 mb-lg-0 me-auto">
          <li class="nav-item">
            <router-link to="/dashboard/products" class="nav-link">產品</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/dashboard/order" class="nav-link">訂單</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/dashboard/coupon" class="nav-link">優惠卷</router-link>
          </li>

        </ul>
        <ul class="navbar-nav mb-2 mb-lg-0">
          <li class="nav-item ms-auto">
            <!-- 點擊觸發logOut method -->
            <a class="nav-link" href="#" @click.prevent="logOut">登出</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  methods: {
    // 登出
    logOut () {
      const api = `${process.env.VUE_APP_API}logout`
      // 登出功能，導向登入頁
      this.$http.post(api)
        .then((res) => {
          if (res.data.success) {
            this.$router.push('/user/login')
          }
        })
    }
  }
}
</script>
