// 後台layout頁面
<template>
  <div>
    <Navbar></Navbar>
    <ToastMessage></ToastMessage>
    <router-view />
  </div>
</template>

<script>
// 導入Navbar元件
import Navbar from '../components/Navbar.vue'
// 匯入mitt
import emitter from '@/methods/emitter'
// 匯入ToastMessage
import ToastMessage from '../components/ToastMessage.vue'

export default {
  created () {
    // 取得登入後cookie的token
    const token = document.cookie.replace(/(?:(?:^|.*;\s*)timToken\s*=\s*([^;]*).*$)|^.*$/, '$1')
    // 將token加入headers
    this.$http.defaults.headers.common.Authorization = token
    // 串接api
    const api = `${process.env.VUE_APP_API}api/user/check`
    // 使用axios方法
    this.$http.post(api)
      .then((res) => {
        // 如果登入失敗，導向登入頁
        if (!res.data.success) {
          this.$router.push('/login')
        }
      })
  },
  components: {
    Navbar,
    ToastMessage
  },
  // provide mitt 讓 子元件可以inject來使用mitt
  provide () {
    return {
      emitter
    }
  }
}
</script>
